* {
  margin: 0;
  padding: 0;
}

html,
body {
  background-color: black;
}

main {
  position: relative;
  height: 1920px;
  width: 1080px;
  overflow: hidden;
  font-size: 3em;
  color: white;
}

section {
  margin: 0;
}
section:first-child {
  height: 570px;
}

h1 {
  font-family: 'Gotham Black', Arial, Helvetica, sans-serif;
  letter-spacing: 4px;
  line-height: 0.94;
}

h2 {
  font-family: 'Gotham Black', Arial, Helvetica, sans-serif;
  margin: 23px 0 18px;
}

h3 {
  font-family: 'Gotham Bold', Arial, Helvetica, sans-serif;
  margin-top: 30px;
  width: 10em;
  line-height: 1.3em;
}
h3.tri {
  opacity: 0.6;
}

#station-title {
  font-family: 'Gotham Medium', Arial, Helvetica, sans-serif;
  color: #33b1ff;
  line-height: 1.3;
}

#today {
  font-family: 'Gotham Book', Arial, Helvetica, sans-serif;
  font-size: 29pt;
}

.temp,
.subh {
  opacity: 0.6;
}

.subh {
  font-family: 'Gotham Bold', Arial, Helvetica, sans-serif;
  .temp {
    opacity: 1;
    margin-left: 0;
  }
}
h2.temp {
  opacity: 1;
}

#moments-tag {
  position: absolute;
  bottom: 29px;
  left: 0;
  width: 698px;
}

#notch-r {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  h4 {
    font-family: 'Gotham Black', Arial, Helvetica, sans-serif;
    font-size: 23pt;
    line-height: 1.2;
  }
  h4:last-child {
    text-align: right;
  }
}

.img-box {
  display: flex;
  justify-content: center;
  align-items: center;
}

#moon-phase {
  text-transform: capitalize;
}

#debug {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100%;
  background: rgba(#000, 0.5);
  color: #fff;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  height: 100%;
  font-size: 2em;
  z-index: 20;
}

#fallback {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  display: block;
}
